import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

// 大背景页
const Main = (resolve) => {
  import('@/main/main.vue').then((module) => {
    resolve(module)
  })
}

// 大背景页二
const Main02 = (resolve) => {
  import('@/main/main02.vue').then((module) => {
    resolve(module)
  })
}

// 初始页
const initPage = (resolve) => {
  import('views/initPage/initPage.vue').then((module) => {
    resolve(module)
  })
}

// 首页
const homePage = (resolve) => {
  import('views/homePage/homePage.vue').then((module) => {
    resolve(module)
  })
}

export default new Router({
  routes: [
    {
      path: '/',
      component: Main,
      children: [
        {
          path: '/',
          name: 'initPage',
          component: initPage
        }
      ]
    },
    {
      path: '/',
      component: Main02,
      children: [
        {
          path: '/homePage',
          name: 'homePage',
          component: homePage
        }
      ]
    }
  ]
})



